    max-width: 1013px;
  }
}

.gallery__container:hover .gallery__img {
  animation-play-state: paused;
}

.gallery__img--second {
  animation-delay: 5s;
}

.gallery__container:hover .gallery__img--second {
  animation-play-state: paused;
}

.gallery__img--third {
  animation-delay: 10s;
}

.gallery__container:hover .gallery__img--third {
  animation-play-state: paused;
}

@keyframes changeGallery {
  0% {
    opacity: 1;
  }

  20% {
    opacity: 1;
  }

  33% {
    opacity: 0;
  }

  87% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

